@mixin mobile {
    @media screen and (max-width: 767.9px) and (min-width: 0) {
      @content;
    }
  }
  
  @mixin tablet {
    @media screen and (min-width: 768px) and (max-width: 1199.9px) {
      @content;
    }
  }
  
  @mixin desktop {
    @media screen and (min-width: 1200px) {
      @content;
    }
  }